const _hoisted_9 = /*#__PURE__*/_createStaticVNode("<section data-v-079a5fd5><p data-v-079a5fd5><h3 data-v-079a5fd5>BACKGROUND AND PURPOSE</h3> Hemorrhage transformation (HT) after acute ischemic stroke (AIS) significantly impacts the prognosis of patients. This study aims to apply machine learning (ML) to develop a reliable HT prediction model for AIS patients based on magnetic resonance imaging (MRI). </p><p data-v-079a5fd5><h3 data-v-079a5fd5>METHODS</h3> A total of 379 AIS patients with 131 in the HT group and 248 in non-HT group were enrolled in this study. All patients received MRI including diffusion-weighted imaging (DWI) and perfusion-weighted imaging (PWI) within 24 hours from symptom onset and had a follow-up CT or MRI within 14 days. The clinical baseline features and MRI parameters (the volume of ADC &lt; 620 × 10⁻⁶ mm²/s, Tmax &gt; 4 s, 6 s, 8 s, and 10 s, PWI-DWI mismatch area, and volume ratio of Tmax &gt; 10 s to Tmax &gt; 6 s [V Tmax&gt;10/V Tmax&gt;6]) were collected for each patient and used as variables for ML. Patients were randomly divided into the model training set (70%, n = 265 with 92 in HT group) and testing set (30%, n = 114 with 39 in HT group). Six representative ML algorithms, including Logistic Regression, Support Vector Machine, Decision Tree, XGBoost, LightGBM, and Random Forest, were trained for HT prediction and 5-fold cross-validation was performed for model optimization. After parameter optimization, the models were assessed on the testing set, and the one with best performance was selected and put in the final model. </p><p data-v-079a5fd5><h3 data-v-079a5fd5>RESULTS</h3> The Logistic Regression algorithm demonstrated superior performance, achieving the highest F1-score of 0.8889, and an area under the curve of 0.96 with a sensitivity of 92.31% and specificity of 92% in the testing set. The MRI parameters of the volume of Tmax &gt; 6 s and V Tmax&gt;10/V Tmax&gt;6 and the National Institutes of Health Stroke Scale score at admission significantly influenced the prediction of HT with feature importance of 0.3343, 0.0813, and 0.029, respectively. The trained model is available on <a style=\"text-decoration:underline;color:black;\" href=\"https://yike-wood.github.io/HT-Predict/\" data-v-079a5fd5>https://yike-wood.github.io/HT-Predict/</a>. </p><p data-v-079a5fd5><h3 data-v-079a5fd5>CONCLUSIONS</h3><span style=\"font-size:large;font-weight:bold;\" data-v-079a5fd5>A ML model</span> based on MRI-PWI can be used to reliably predict HT for AIS patients with high sensitivity and specificity, which may assist radiologists and clinicians in timely assessing HT risk after MRI examinations. </p></section>", 1);
