import { createRouter, createWebHashHistory } from 'vue-router'
import SweetHome from './components/SweetHome.vue'
import MoTools from './components/MoTools.vue'
import MoCalculator from './components/MoCalculator.vue'

const routes = [
  { path: '/', component: SweetHome },
  { path: '/Calculator', component: MoCalculator },
  { path: '/Tools', component: MoTools }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
