import { ref } from 'vue';
export default {
  data() {
    return {
      varTmaxO6mL: ref(''),
      varTmaxO10sDTmaxO6s: ref(''),
      varNIHSS: ref(''),
      varRisk: 0
    };
  },
  methods: {
    onSubmit(values) {
      console.log('submit', values);
      console.log('vars: ', this.varTmaxO6mL, this.varTmaxO10sDTmaxO6s, this.varNIHSS);
      let var0 = parseFloat(values.var0);
      let var1 = parseFloat(values.var1);
      let var2 = parseFloat(values.var2);
      let var0sc = (var0 - 64.09811320754717) / 78.48224048733397;
